import React from 'react'
import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import one from './components/b1.jpg';
import s from './components/b2.jpg';
import rake from './components/b3.jpg';
import Navbar from './components/Navbar';
import { Link } from "react-router-dom";
export default function Mobile() {
   const Mobile=[{
    "img":<img src={one} width={250} height={150} />,
     "modal":"Deluxe ",
     "Type":"Single Bed + AC",
     "price":"$ 200",
     "NOTE":"Price is applicable for 24 hours only",
     "available":true
   },
   {
    "img":<img src={rake} width={250} height={150} />,
    "modal":"Deluxe ",
    "Type":"Two Bed + AC +Attached Restroom",
    "price":"$ 400",
    "NOTE":"Price is applicable for 24 hours only",
    "available":true
   },
   {
    "img":<img src={s} width={250} height={150} />,
    "modal":" Normal",
     "Type":"Three Bed(Non-AC)",
     "price":"$ 300",
     "NOTE":"Price is applicable for 24 hours only",
     "available":true
   }
]
  return (
    <div>
      <Navbar/>
    <div class="phone">
        <hr width="100%" size="3" /><center><h1>Rooms</h1></center><hr width="100%" size="3" />
      <Box p={10}>
      <Grid  container justifyContent="center" spacing={2}>
              {Mobile.map((sp)=>(<Grid item>
               <Paper key={sp.id} sx={{height:400 , width:250}}style={{border: '2px solid green'}}>
                   {sp.img}<br/>{sp.Name}<br/>
                   <h4>modal:{sp.modal}<br/>price:{sp.price}<br/>NOTE:{sp.NOTE}<br/>available:{sp.available ? "Yes":"No"}</h4>
                   <Button variant="contained"><Link to='/Payment'>BOOK NOW</Link> </Button>
               </Paper>
              </Grid>))}
          </Grid>
     </Box>
     </div>
    </div>
)
}