import React from 'react'
import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import one from './min.jpg';
import s from './var.jpg';
import rake from './lit.jpg';
import { Link } from "react-router-dom";
import Navbar from './Navbar';
import { BorderAllOutlined } from '@mui/icons-material';
export default function Mobile() {
   const Mobile=[{
    "img":<img src={one} width={250} height={150} />,
     "City Name":"Vijayawada ",
     "Location Name":"Snajiv Nagar",
     "Hotel Name":"Minerva Grand",
     "available":true
   },
   {
    "img":<img src={rake} width={250} height={150} />,
    "City Name":"Vijayawada ",
    "Location Name":"Rajiv Nagar",
    "Hotel Name":"Little Village",
    "available":true
   },
   {
    "img":<img src={s} width={250} height={150} />,
    "City Name":"Vijayawada ",
     "Location Name":"Snajiv Nagar",
     "Hotel Name":"Veranda Cafe",
     "available":true
   }
]
  return (
    <div>
      <Navbar/>
    <div class="phone">
        <hr width="100%" size="3" /><center><h2> Book Food Items</h2></center><hr width="100%" size="3" />
      <Box p={10}>
      <Grid  container justifyContent="center" spacing={2}>
              {Mobile.map((sp)=>(<Grid item>
               <Paper key={sp.id} sx={{height:400 , width:250} } style={{border: '2px solid yellow'}}>
               {sp.img}<br/>{sp.Name}<br/>
                   <h3>City Name:{sp['City Name']}<br/>Location Name:{sp['Location Name']}<br/>Hotel Name:{sp['Hotel Name']}<br/>available:{sp.available ? "Yes":"No"}</h3>
                   <Button variant="contained"><Link to='/Food'>MORE INFO</Link> </Button>
               </Paper>
               
              </Grid>))}
          </Grid>
     </Box>
     </div>
    </div>
)
}