import React from 'react'
import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import one from './no.jpg';
import s from './ma.jpg';
import rake from './ai.jpg';
import { Link } from "react-router-dom";
import { BorderAllOutlined } from '@mui/icons-material';
import Navbar from './Navbar';
export default function Mobile() {
   const Mobile=[{
    "img":<img src={one} width={300} height={250} />,
     "City Name":"Vijayawada ",
     "Location Name":"Benz Circle",
     "Hotel Name":"Novotel Varun",
     "available":true
   },
   {
    "img":<img src={rake} width={300} height={250} />,
    "City Name":"Vijayawada ",
    "Location Name":"Rajiv Nagar",
    "Hotel Name":"Hotel Aira",
    "available":true
   },
   {
    "img":<img src={s} width={300} height={250} />,
    "City Name":"Vijayawada ",
     "Location Name":"Kanuru",
     "Hotel Name":"Hotel Manorama",
     "available":true
   }
]
  return (
    <div>
      <Navbar/>
    <div class="phone">
        <hr width="100%" size="3" /><center><h2> Book Rooms</h2></center><hr width="100%" size="3" />
      <Box p={10}>
      <Grid  container justifyContent="center" spacing={2}>
              {Mobile.map((sp)=>(<Grid item>
               <Paper key={sp.id} sx={{height:500 , width:350} } style={{border: '2px solid yellow'}}>
               {sp.img}<br/>{sp.Name}<br/>
                   <h3>City Name:{sp['City Name']}<br/>Location Name:{sp['Location Name']}<br/>Hotel Name:{sp['Hotel Name']}<br/>available:{sp.available ? "Yes":"No"}</h3>
                   <Button variant="contained"><Link to='/Services'>MORE INFO</Link> </Button>
               </Paper>
               
              </Grid>))}
          </Grid>
     </Box>
     </div>
    </div>
)
}